<template>
    <div id="problem-list">
        <loadings></loadings>
        <el-card class="box-card" style="margin-top: 60px;">
            <el-table :data="problem" style="width: 100%;" cell-style="cursor: pointer">
                <el-table-column label="ID" min-width="100%" align="center">
                    <template slot-scope="scope">
                        <div>{{ scope.row.problemId }}</div>
                    </template>
                </el-table-column>
                <el-table-column label="标题" min-width="530%">
                    <template slot-scope="scope">
                        <div>{{ scope.row.title }}</div>
                    </template>
                </el-table-column>
                <el-table-column label="创建时间" min-width="190%">
                    <template slot-scope="scope">
                        <div>{{format_time(scope.row.createTime)}}</div>
                    </template>
                </el-table-column>
                <el-table-column label="是否发布" min-width="100%" align="center">
                    <template slot-scope="scope">
                        <el-switch
                                @change="switchProblemState(scope.row.problemId, scope.row.isUsed=='0'?0:1, scope.$index)"
                                style="margin-top: -4px;" v-model="scope.row.isUsed" active-value="1"
                                inactive-value="0" active-color="#13ce66" inactive-color="#ff4949">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="创建人" min-width="150%" align="center">
                    <template slot-scope="scope">
                        <div>{{scope.row.creatorNick}}</div>
                    </template>
                </el-table-column>
                <el-table-column align="center" min-width="120%">
                    <template slot-scope="scope">
                        <div style="text-align: left">
                            <el-button
                                    @click="$router.push({path: '/problem/problem/modify_content',query: {pid: scope.row.problemId}})"
                                    style="padding: 4px 8px;margin-right: 5px"
                                    type="primary" size="mini">编辑
                            </el-button>
                            <el-button style="padding: 4px 8px;" type="danger" size="mini">删除
                            </el-button>
                        </div>
                        <div style="margin-top: 5px;text-align: left">
                            <el-button style="padding: 4px 8px;"
                                       @click="$router.push({path: '/problem/problem/modify_testcase',query: {pid: scope.row.problemId}})"
                                       type="warning" size="mini">测试数据
                            </el-button>
                        </div>
                        <div style="margin-top: 5px;text-align: left">
                            <el-button style="padding: 4px 8px;" type="success" size="mini">分析</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>

<script>
    import '@/assets/js/include.js'
    import Loadings from '@/components/loading/loading'
    import fun from '@/assets/js/OJFunction'

    export default {
        name: "list",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                },
                problem: {
                    problemId: "",
                    title: "",
                    createTime: 0,
                    isUsed: "",
                    creatorNick: ""
                }
            }
        },
        methods: {
            switchProblemState(id, o, i) {
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/switch/statue',
                    {
                        is_used: o,
                        problem_id: id
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {

                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.$message({
                            message: '操作成功',
                            type: 'success'
                        });
                        var remember_me = res.body.OJ_USER_INFO.saved;
                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];

                        if (remember_me) {
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                        }

                    } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                        // alert(55)
                        this.$message.error('操作失败，您未登录');
                        window.location.href = "/admin/login.html";
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_NEWS) {
                        this.$message.error('无效的操作');
                        this.news[i].isUsed = this.news[i].isUsed == '0' ? '1' : '0';
                    }
                });
            },
            format_time: function (s) {
                return fun.format_time(s)
            },
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");

            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/home/list',
                {}, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                console.log(res.body)
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    // this.page_setting.login = true
                    this.problem = res.body.OJ_PROBLEM
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }

                    for (var i = 0; i < this.problem.length; i++) {
                        this.problem[i].isUsed = this.problem[i].isUsed + "";
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    window.location.href = "/admin/login.html";
                }
            });
        }
    }
</script>

