<template>
    <div id="news-list">
        <loadings></loadings>
        <el-card class="box-card" style="margin-top: 60px;">
            <el-table :data="news" style="width: 100%;" cell-style="cursor: pointer">
                <el-table-column label="ID" min-width="100%" align="center">
                    <template slot-scope="scope">
                        <div @click="showNews(scope.$index)">{{ scope.row.newsId }}</div>
                    </template>
                </el-table-column>
                <el-table-column label="标题" min-width="290%">
                    <template slot-scope="scope">
                        <div @click="showNews(scope.$index)">{{ scope.row.title }}</div>
                    </template>
                </el-table-column>
                <el-table-column label="内容" min-width="290%">
                    <template slot-scope="scope">
                        <div @click="showNews(scope.$index)">{{delHtmlTag(scope.row.content)}}</div>
                    </template>
                </el-table-column>
                <el-table-column label="创建时间" min-width="170%">
                    <template slot-scope="scope">
                        <div @click="showNews(scope.$index)">{{format_time(scope.row.publishTime)}}</div>
                    </template>
                </el-table-column>
                <el-table-column label="是否发布" min-width="100%" align="center">
                    <template slot-scope="scope">
                        <el-switch @change="switchNewsState(scope.row.newsId, scope.row.isUsed=='0'?0:1, scope.$index)"
                                   style="margin-top: -4px;" v-model="scope.row.isUsed" active-value="1"
                                   inactive-value="0" active-color="#13ce66" inactive-color="#ff4949">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="发布用户" min-width="120%" align="center">
                    <template slot-scope="scope">
                        <div @click="showNews(scope.$index)">{{scope.row.publisherNick}}</div>
                    </template>
                </el-table-column>
                <el-table-column v-bind:label="'　'" align="center" min-width="140%">
                    <template slot-scope="scope">
                        <el-button @click="preEdit(scope.row.newsId)" style="padding: 4px 8px;margin-right: 5px"
                                   type="primary"
                                   size="mini">编辑
                        </el-button>
                        <el-button @click="preDele(scope.$index)" style="padding: 4px 8px;" type="danger"
                                   size="mini">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <el-dialog v-bind:title="news[show.now].title" :visible.sync="show.dialogVisible" width="30%">
            <span v-html="news[show.now].content"></span>
            <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="show.dialogVisible = false">确 定</el-button>
                </span>
        </el-dialog>



        <!---->
    </div>

</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    /* eslint no-undef: "off"*/
    /* eslint no-unused-vars: "off"*/
    /* eslint no-irregular-whitespace: "off"*/

    import fun from '@/assets/js/OJFunction'
    import '@/assets/js/include.js'
    import Loadings from '@/components/loading/loading'

    export default {
        name: "news",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: ""
                },
                news: [
                    {
                        content: "",
                        title: "",
                        importance: 0,
                        isUsed: "0",
                        publishTime: 0,
                        publisherNick: ""
                    },
                ],
                show: {
                    now: 0,
                    dialogVisible: false,
                },
                dele: {
                    now: 0,
                    dialogVisible: false,
                }
            }
        },
        methods: {
            preEdit(i) {
                this.$router.push({
                    path: "/news/news/modify",
                    query: {
                        nid: i
                    }
                })
            },
            showNews(i) {
                this.show.now = i
                this.show.dialogVisible = true
            },
            preDele(i) {
                this.dele.now = i
                this.dele.dialogVisible = true
            },
            deleNews(id) {
                this.dele.dialogVisible = false
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/news/dele',
                    {
                        news_id: id
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {

                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.$message({
                            message: '操作成功',
                            type: 'success'
                        });
                        var remember_me = res.body.OJ_USER_INFO.saved;
                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];

                        if (remember_me) {
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                        }
                        var _this = this;
                        setTimeout(function () {
                            _this.$router.go(0);
                        }, 800)
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                        this.$message.error('操作失败，您未登录');
                        window.location.href = "/admin/login.html";
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_NEWS) {
                        this.$message.error('无效的操作');

                    }
                });
            },
            switchNewsState(id, o, i) {
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/news/switch/statue',
                    {
                        is_used: o,
                        news_id: id
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {

                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.$message({
                            message: '操作成功',
                            type: 'success'
                        });
                        var remember_me = res.body.OJ_USER_INFO.saved;
                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];

                        if (remember_me) {
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                        }

                    } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                        // alert(55)
                        this.$message.error('操作失败，您未登录');
                        window.location.href = "/admin/login.html";
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_NEWS) {
                        this.$message.error('无效的操作');
                        this.news[i].isUsed = this.news[i].isUsed == '0' ? '1' : '0';
                    }
                });
            },
            delHtmlTag: function (s) {
                return s.replace(/<[^>]+>/g, "");
            },
            format_time: function (s) {
                return fun.format_time(s)
            },
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");

            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/news/home/list',
                {}, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                console.log(res.body)
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.news = res.body.OJ_NEWS
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }

                    for (var i = 0; i < this.news.length; i++) {
                        this.news[i].isUsed = this.news[i].isUsed + "";
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    this.$message.error('操作失败，您未登录');
                    window.location.href = "/admin/login.html";
                }
            });
        }
    }
</script>

<style lang="scss">
    #news-list {
        .layui-form-switch {
            margin-top: 0px !important;
        }
    }
</style>