import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '@/components/index/index'
import NewsList from '@/components/news/news/list'
import NewsModify from '@/components/news/news/modify'
import NewsAdd from '@/components/news/news/add'
import ProblemList from '@/components/problem/problem/list'
import ProblemModifyContent from '@/components/problem/problem/modify_content'
import ProblemModifyOption from '@/components/problem/problem/modify_option'
import ProblemModifyTestcase from '@/components/problem/problem/modify_testcase'
import ProblemAddContent from '@/components/problem/problem/add_content'
import ProblemAddOption from '@/components/problem/problem/add_option'
import ProblemAddTestcase from '@/components/problem/problem/add_testcase'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'index',
    component: Index,
}, {
    path: '/news/news/list',
    name: 'news_list',
    component: NewsList,
}, {
    path: '/news/news/modify',
    name: 'news_modify',
    component: NewsModify,
}, {
    path: '/news/news/add',
    name: 'news_add',
    component: NewsAdd,
}, {
    path: '/problem/problem/list',
    name: 'problem_list',
    component: ProblemList,
}, {
    path: '/problem/problem/modify_content',
    name: 'problem_modify_content',
    component: ProblemModifyContent,
}, {
    path: '/problem/problem/modify_option',
    name: 'problem_modify_option',
    component: ProblemModifyOption,
}, {
    path: '/problem/problem/modify_testcase',
    name: 'problem_modify_testcase',
    component: ProblemModifyTestcase,
}, {
    path: '/problem/problem/add_content',
    name: 'problem_add_content',
    component: ProblemAddContent,
}, {
    path: '/problem/problem/add_option',
    name: 'problem_add_option',
    component: ProblemAddOption,
}, {
    path: '/problem/problem/add_testcase',
    name: 'problem_add_testcase',
    component: ProblemAddTestcase,
}]

export default new VueRouter({
    mode: 'hash',
    routes: routes
})