<template>
    <div id="problem-add">
        <loadings></loadings>
        <el-card class="box-card" style="margin-top: 60px;">
            <el-steps :active="2" align-center>
                <el-step title="步骤1" description="修改题目内容"></el-step>
                <el-step title="步骤2" description="修改题目参数"></el-step>
                <el-step title="步骤3" description="添加测试数据"></el-step>
            </el-steps>
            <el-row :gutter="20">
                <el-col :span="12" style="text-align: center">
                    <el-card class="box-card" style="margin-top: 60px;" shadow="never">

                        <el-table
                                ref="multipleTable"
                                :data="testcase"
                                tooltip-effect="dark"
                                v-loading="updating"
                                style="width: 100%;">
                            <el-table-column
                                    label="序号"
                                    min-width="60%"
                                    align="center">
                                <template slot-scope="scope">{{ scope.$index+1 }}</template>
                            </el-table-column>
                            <el-table-column
                                    label="文件名"
                                    min-width="120%">
                                <template slot-scope="scope">
                                    {{scope.$index + 1 + '.in'}}
                                    <span style="font-weight: bold">{{'  /  '}}</span>
                                    {{scope.$index + 1 + '.out'}}
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="name"
                                    label="大小"
                                    min-width="60%">
                                <template slot-scope="scope">
                                    {{transsize(scope.row.inSize)}}
                                    <span style="font-weight: bold">{{'  /  '}}</span>
                                    {{transsize(scope.row.outSize)}}
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="address"
                                    label="操作"
                                    show-overflow-tooltip
                                    min-width="80%"
                                    align="center">
                                <template>
                                    <el-button icon="el-icon-plus" circle size="mini"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-card>
                </el-col>

                <el-col :span="12" style="text-align: center">
                    <el-card class="box-card" style="margin-top: 60px;" shadow="never">
                        <div slot="header" class="clearfix" style="text-align: left">
                            <span style="text-align: left">添加数据</span>
                        </div>
                        <el-upload
                                class="upload-demo" drag
                                v-bind:action="GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/add_testcase'"
                                :data="{'problem_id': $route.query.pid}" :on-success="afterSuccessload"
                                :before-upload="beforeUpload" :on-error="afterErrorload" :on-remove="afterErrorload"
                                :headers="{'OJ_ACCESS_TOKEN':page_setting.access_token, 'OJ_REFRESH_TOKEN': page_setting.refresh_token}"
                                accept=".zip" auto-upload ref="upload" :limit="1">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传zip文件，上传前会清空当前已存在的数据文件！</div>
                        </el-upload>
                    </el-card>
                </el-col>
            </el-row>
            <div style="text-align: center;margin-top: 30px;margin-bottom: 20px">
                <el-button @click="edit" type="primary" size="small">确认</el-button>
            </div>
        </el-card>

    </div>
</template>

<script>
    import '@/assets/js/include.js'
    import Loadings from '@/components/loading/loading'
    import fun from '@/assets/js/OJFunction'
    export default {
        name: "add_testcase",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: ""
                },
                testcase: "",
                updating: false
            }
        },
        methods: {
            edit(){
                this.$router.push({
                    path: "/problem/problem/list",
                })
            },
            transsize(i) {
                if (i < 1000) return i + "B";
                i = i / 1024;
                if (i < 1000) return i.toFixed(1) + "K";
                i = i / 1024;
                if (i < 1000) return i.toFixed(1) + "M";
                i = i / 1024;
                if (i < 1000) return i.toFixed(1) + "G";
                i = i / 1024;
                return i.toFixed(1) + "T";
            },
            afterSuccessload(res) {
                console.log(res)
                var error_type = res.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.testcase = res.OJ_TESTCASE;
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    window.location.href = "/admin/login.html";
                }
                var _this = this;
                this.updating = false;
                setTimeout(function () {
                    _this.$refs.upload.clearFiles();
                }, 1000)
                console.log(res)
            },
            afterErrorload() {
                this.updating = false;
                var _this = this;
                setTimeout(function () {
                    _this.$refs.upload.clearFiles();
                }, 1000)
            },
            beforeUpload() {
                this.updating = true;
            }
        },
        created() {
            if (this.$route.query.pid == null || this.$route.query.pid == "") {
                this.$router.push({
                    path: "/problem/problem/list",
                })
            }
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");

            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/home/modify_testcase',
                {
                    problem_id: this.$route.query.pid
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.testcase = res.body.OJ_TESTCASE;
                    console.log(this.testcase)
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    window.location.href = "/admin/login.html";
                }
                $("html,body").animate({scrollTop: "0px"}, 0);
            }, function () {
                $("html,body").animate({scrollTop: "0px"}, 0);
            });
        }
    }
</script>

<style scoped>

</style>