<template>
    <div id="problem-add">
        <loadings></loadings>
        <el-card class="box-card" style="margin-top: 60px;">
            <el-steps :active="1" align-center>
                <el-step title="步骤1" description="修改题目内容"></el-step>
                <el-step title="步骤2" description="修改题目参数"></el-step>
                <el-step title="步骤3" description="添加测试数据"></el-step>
            </el-steps>
            <!--<div>-->
                <!--<el-button @click="last_step" type="text" style="float: left">{{"&lt;&lt; 上一步"}}</el-button>-->
            <!--</div>-->
            <div style="clear: both"></div>
            <el-form ref="form" :model="problem" label-width="80px" style="margin-top: 15px;">
                <el-timeline>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="标题">
                            <el-input v-model="problem.title" style="width: 50%"
                                      :disabled="true">
                                <template slot="prepend">{{"ID "+ problem.problemId}}</template>
                            </el-input>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="是否发布">
                            <el-switch style="margin-top: -4px;" v-model="problem.isUsed" active-value="1"
                                       inactive-value="0" active-color="#13ce66" inactive-color="#ff4949">
                            </el-switch>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="时间">
                            <el-input type="number" v-model="problem.timeLimit"
                                      style="width: 25%">
                                <template slot="append">ms</template>
                            </el-input>
                            <small style="margin-left: 15px;color: red;font-weight: bold">
                                {{(problem.timeLimit/1000).toFixed(1) + " s"}}
                            </small>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="内存">
                            <el-input type="number" v-model="problem.memoryLimit"
                                      style="width: 25%">
                                <template slot="append">KB</template>
                            </el-input>
                            <small style="margin-left: 15px;color: red;font-weight: bold">
                                {{(problem.memoryLimit/1024).toFixed(1) + " MB"}}
                            </small>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="特判">
                            <el-switch style="margin-top: -4px;" v-model="problem.isSpj" active-value="1"
                                       inactive-value="0" active-color="#13ce66" inactive-color="#ff4949">
                            </el-switch>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="分类">
                            <div id="ProblemCategory" style="line-height: 30px;margin-top: 5px;margin-bottom: 5px">
                                <el-checkbox @change="delCate(i)" checked size="medium"
                                             v-for="(cate, i) in problem.category" v-bind:key="key"
                                             v-bind:label="cate.name" border v-bind:color="cate.color"></el-checkbox>
                                <el-button
                                        @click="chooseCate" type="primary" icon="el-icon-edit" plain
                                        size="medium" style="margin-left: 10px"></el-button>
                            </div>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="来源">
                            <div id="ProblemSource" style="line-height: 30px;margin-top: 5px;margin-bottom: 5px">
                                <el-checkbox @change="delSour(i)" checked size="medium"
                                             v-for="(sour, i) in problem.source" v-bind:key="key"
                                             v-bind:label="sour.name" border v-bind:color="sour.color"></el-checkbox>
                                <el-button
                                        @click="chooseSour" type="primary" icon="el-icon-edit" plain
                                        size="medium" style="margin-left: 10px"></el-button>
                            </div>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="purple">
                        <div style="text-align: center;margin-top: 30px;margin-bottom: 20px">
                            <el-button @click="edit" type="primary" size="small">下一步</el-button>
                        </div>
                    </el-timeline-item>
                </el-timeline>
            </el-form>
            <!--<button @click="test"></button>-->
        </el-card>
        <el-dialog :visible.sync="category.dialogVisible" width="80%">
            <el-collapse v-model="category.data" v-if="category.lineCategory.length != 0">
                <el-collapse-item v-for="(maincate, i) in category.category" v-bind:key="key"
                                  v-bind:title="maincate.main.name" v-bind:name="i" active>
                    <div style="margin-left: 20px;margin-top: 10px">
                        <el-collapse v-model="category.data">
                            <el-collapse-item title="基本算法" active v-if="maincate.root.length > 0">
                                <div style="margin-top: 10px">
                                    <el-checkbox @change="upPreCate()" size="medium" v-for="(rootcate) in maincate.root"
                                                 v-bind:key="key"
                                                 v-bind:label="rootcate.name" border
                                                 v-bind:color="rootcate.color"
                                                 v-model="category.lineCategory[getCheckCateId(rootcate.cateId)].check"></el-checkbox>
                                </div>
                            </el-collapse-item>
                            <el-collapse-item v-for="(norootcate) in maincate.noroot"
                                              v-bind:title="norootcate.main.name" v-bind:key="key" active>
                                <div style="margin-top: 10px">
                                    <el-checkbox
                                            @change="upPreCate()" size="medium" v-for="(rootcate) in norootcate.root"
                                            v-bind:key="key"
                                            v-bind:label="rootcate.name" border
                                            v-bind:color="rootcate.color"
                                            v-model="category.lineCategory[getCheckCateId(rootcate.cateId)].check"></el-checkbox>
                                </div>
                            </el-collapse-item>
                        </el-collapse>
                    </div>
                </el-collapse-item>
            </el-collapse>
            <div style="margin-top: 20px">
                <span>已选分类：</span>
                <el-checkbox @change="delPreCate(cate.cateId)" size="medium"
                             v-for="(cate) in category.preCategory" v-bind:key="key"
                             v-bind:label="cate.name" border v-bind:color="cate.color"
                             v-model="category.lineCategory[getCheckCateId(cate.cateId)].check"></el-checkbox>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="sureCate">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog :visible.sync="source.dialogVisible" width="80%">
            <el-collapse v-model="source.data" v-if="source.lineSource.length != 0">
                <el-collapse-item v-for="(mainsour, i) in source.source" v-bind:key="key"
                                  v-bind:title="mainsour.main.name" v-bind:name="i" active>
                    <div style="margin-left: 20px;margin-top: 10px">
                        <el-collapse v-model="source.data">
                            <el-collapse-item title="基本来源" active v-if="mainsour.root.length > 0">
                                <div style="margin-top: 10px">
                                    <el-checkbox @change="upPreSour()" size="medium" v-for="(rootsour) in mainsour.root"
                                                 v-bind:key="key"
                                                 v-bind:label="rootsour.name" border
                                                 v-bind:color="rootsour.color"
                                                 v-model="source.lineSource[getCheckSourId(rootsour.sourceId)].check"></el-checkbox>
                                </div>
                            </el-collapse-item>
                            <el-collapse-item v-for="(norootsour) in mainsour.noroot"
                                              v-bind:title="norootsour.main.name" v-bind:key="key" active>
                                <div style="margin-top: 10px">
                                    <el-checkbox
                                            @change="upPreSour()" size="medium" v-for="(rootsour) in norootsour.root"
                                            v-bind:key="key"
                                            v-bind:label="rootsour.name" border
                                            v-bind:color="rootsour.color"
                                            v-model="source.lineSource[getCheckSourId(rootsour.sourceId)].check"></el-checkbox>
                                </div>
                            </el-collapse-item>
                        </el-collapse>
                    </div>
                </el-collapse-item>
            </el-collapse>
            <div style="margin-top: 20px">
                <span>已选分类：</span>
                <el-checkbox @change="delPreSour(sour.sourceId)" size="medium"
                             v-for="(sour) in source.preSource" v-bind:key="key"
                             v-bind:label="sour.name" border v-bind:color="sour.color"
                             v-model="source.lineSource[getCheckSourId(sour.sourceId)].check"></el-checkbox>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="sureSour">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    import '@/assets/js/include.js'
    import Loadings from '@/components/loading/loading'
    import Vue from 'vue'
    import fun from '@/assets/js/OJFunction'

    export default {
        name: "add_option",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: ""
                },
                problem: {
                    problemId: "",
                    title: "",
                    isUsed: "",
                    isSpj: "",
                    timeLimit: 0,
                    memoryLimit: 0,
                    category: [{
                        cateId: 0
                    }],
                    source: [{
                        sourceId: 0
                    }]
                },
                category: {
                    category: "",
                    dialogVisible: false,
                    preCategory: "",
                    lineCategory: []
                },
                source: {
                    source: "",
                    dialogVisible: false,
                    preSource: "",
                    lineSource: []
                },
            }
        },
        methods: {
            edit: function () {
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/add_option',
                    {
                        problem_id: this.problem.problemId,
                        is_used: this.problem.isUsed,
                        time_limit: this.problem.timeLimit,
                        memory_limit: this.problem.memoryLimit * 1024,
                        is_spj: this.problem.isSpj,
                        category: JSON.stringify(this.problem.category),
                        source: JSON.stringify(this.problem.source)
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.$message({
                            message: '操作成功',
                            type: 'success'
                        });
                        var remember_me = res.body.OJ_USER_INFO.saved;
                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];

                        if (remember_me) {
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                        }

                        this.$router.push({
                            path: "/problem/problem/add_testcase",
                            query: {
                                pid: this.problem.problemId
                            }
                        })


                    } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                        this.$message.error('操作失败，您未登录');
                        window.location.href = "/admin/login.html";
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_NEWS) {
                        this.$message.error('无效的操作');
                    }
                });
            },
            sureCate: function () {
                this.category.dialogVisible = false;
                this.problem.category = this.category.preCategory;
            },
            sureSour: function () {
                this.source.dialogVisible = false;
                this.problem.source = this.source.preSource;
            },
            chooseCate: function () {
                this.category.preCategory = this.problem.category;
                for (let i = 0; i < this.category.lineCategory.length; i++) {
                    this.category.lineCategory[i].check = false;
                }
                for (let i = 0; i < this.category.preCategory.length; i++) {
                    let index = this.getCheckCateId(this.category.preCategory[i].cateId);
                    this.category.lineCategory[index].check = true;
                }
                this.category.dialogVisible = true;
            },
            chooseSour: function () {
                this.source.preSource = this.problem.source;
                for (let i = 0; i < this.source.lineSource.length; i++) {
                    this.source.lineSource[i].check = false;
                }
                for (let i = 0; i < this.source.preSource.length; i++) {
                    let index = this.getCheckSourId(this.source.preSource[i].sourceId);
                    this.source.lineSource[index].check = true;
                }
                this.source.dialogVisible = true;
            },
            getCheckCateId: function (id) {
                var l = 0, r = this.category.lineCategory.length - 1;
                while (l < r) {
                    var mid = (l + r) >> 1;
                    if (this.category.lineCategory[mid].cateId < id) l = mid + 1;
                    else r = mid;
                }
                return l;
            },
            getCheckSourId: function (id) {
                var l = 0, r = this.source.lineSource.length - 1;
                while (l < r) {
                    var mid = (l + r) >> 1;
                    if (this.source.lineSource[mid].sourceId < id) l = mid + 1;
                    else r = mid;
                }
                return l;
            },
            delCate: function (i) {
                this.problem.category.splice(i, 1);
            },
            delSour: function (i) {
                this.problem.source.splice(i, 1);
            },
            upPreCate: function () {
                this.category.preCategory = [];
                for (let i = 0; i < this.category.lineCategory.length; i++) {
                    if (this.category.lineCategory[i].check) {
                        this.category.preCategory.push(this.category.lineCategory[i]);
                    }
                }
            },
            upPreSour: function () {
                this.source.preSource = [];
                for (let i = 0; i < this.source.lineSource.length; i++) {
                    if (this.source.lineSource[i].check) {
                        this.source.preSource.push(this.source.lineSource[i]);
                    }
                }
            },
            delPreCate: function (id) {
                let index = this.getCheckCateId(id);
                this.category.lineCategory[index].check = false;
                this.upPreCate();
            },
            delPreSour: function (id) {
                let index = this.getCheckSourId(id);
                this.source.lineSource[index].check = false;
                this.upPreSour();
            },
            random_color: function () {
                var color_theme = new Array("#F44336", "#ff9800", "#4CAF50", "#8bc34a", "#00bcd4", "#2196F3", "#9c27b0", "#3f51b5", "#009688", "#e91e63");
                var x = Math.floor(((Math.random() * color_theme.length) ^ (Math.random() * color_theme.length) ^ (Math.random() * color_theme.length)) % color_theme.length);
                return color_theme[x];
            }
        },
        created() {
            if (this.$route.query.pid == null || this.$route.query.pid == "") {
                this.$router.push({
                    path: "/problem/problem/list",
                })
            }
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/home/add_option',
                {
                    problem_id: this.$route.query.pid
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                console.log(res.body);
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.problem = res.body.OJ_PROBLEM
                    this.problem.isUsed = this.problem.isUsed + ""
                    this.problem.isSpj = this.problem.isSpj + ""
                    this.problem.memoryLimit /= 1024

                    console.log(this.problem)
                    this.category.category = res.body.OJ_CATEGORY
                    this.source.source = res.body.OJ_SOURCE
                    for (let i = 0; i < this.category.category.length; i++) {
                        for (let j = 0; j < this.category.category[i].root.length; j++) {
                            Vue.set(this.category.category[i].root[j], "color", this.random_color());
                            Vue.set(this.category.category[i].root[j], "check", false);
                            Vue.set(this.category.lineCategory, this.category.lineCategory.length, this.category.category[i].root[j]);
                            console.log(this.category.category[i].root[j])
                        }
                        for (let j = 0; j < this.category.category[i].noroot.length; j++) {
                            for (let k = 0; k < this.category.category[i].noroot[j].root.length; k++) {
                                Vue.set(this.category.category[i].noroot[j].root[k], "color", this.random_color());
                                Vue.set(this.category.category[i].noroot[j].root[k], "check", false);
                                Vue.set(this.category.lineCategory, this.category.lineCategory.length, this.category.category[i].noroot[j].root[k]);
                            }
                        }
                    }
                    for (let i = 0; i < this.source.source.length; i++) {
                        for (let j = 0; j < this.source.source[i].root.length; j++) {
                            Vue.set(this.source.source[i].root[j], "color", this.random_color());
                            Vue.set(this.source.source[i].root[j], "check", false);
                            Vue.set(this.source.lineSource, this.source.lineSource.length, this.source.source[i].root[j]);
                            console.log(this.source.source[i].root[j])
                        }
                        for (let j = 0; j < this.source.source[i].noroot.length; j++) {
                            for (let k = 0; k < this.source.source[i].noroot[j].root.length; k++) {
                                Vue.set(this.source.source[i].noroot[j].root[k], "color", this.random_color());
                                Vue.set(this.source.source[i].noroot[j].root[k], "check", false);
                                Vue.set(this.source.lineSource, this.source.lineSource.length, this.source.source[i].noroot[j].root[k]);
                            }
                        }
                    }

                    this.category.lineCategory.sort(function (a, b) {
                        return a.cateId - b.cateId;
                    })
                    this.source.lineSource.sort(function (a, b) {
                        return a.sourceId - b.sourceId;
                    })

                    for (let i = 0; i < this.problem.category.length; i++) {
                        let index = this.getCheckCateId(this.problem.category[i].cateId)
                        this.category.lineCategory[index].check = true;
                        Vue.set(this.problem.category[i], "color", this.category.lineCategory[index].color);
                    }
                    for (let i = 0; i < this.problem.source.length; i++) {
                        let index = this.getCheckSourId(this.problem.source[i].sourceId)
                        this.source.lineSource[index].check = true;
                        Vue.set(this.problem.source[i], "color", this.source.lineSource[index].color);
                    }
                    this.category.preCategory = this.problem.category;
                    this.source.preSource = this.problem.source;
                    console.log(this.source.lineSource)
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];
                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    window.location.href = "/admin/login.html";
                }
                $("html,body").animate({scrollTop: "0px"}, 0);
            }, function () {
                $("html,body").animate({scrollTop: "0px"}, 0);
            });
        }
    }
</script>

<style scoped>

</style>