<template>
    <div id="news-add">
        <loadings></loadings>
        <el-card class="box-card" style="margin-top: 60px;">
            <el-form ref="form" :model="news" label-width="80px" style="margin-top: 15px">
                <el-form-item label="标题">
                    <el-input v-model="news.title" style="width: 50%"></el-input>
                </el-form-item>
                <el-form-item label="是否发布">
                    <el-switch v-model="news.isUsed" style="margin-top: -4px;" active-value="1"
                               inactive-value="0" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
                </el-form-item>
                <el-form-item label="内容">
                    <div id="vditor"></div>
                </el-form-item>
            </el-form>
            <div style="text-align: center;margin-top: 30px;margin-bottom: 20px">
                <el-button @click="add" type="primary">添加</el-button>
            </div>

        </el-card>
    </div>
</template>

<script>
    import Loadings from '@/components/loading/loading'
    import Vditor from "vditor"
    import '@/assets/js/include.js'
    import fun from '@/assets/js/OJFunction'

    export default {
        name: "add",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: ""
                },
                news: {
                    content: "",
                    title: "",
                    //     importance: 0,
                    //     isUsed: "0",
                    //     publishTime: 0,
                    //     publisherNick: ""
                },
                contentEditor: ''
            }
        },
        methods: {
            add: function () {
                this.news.content = this.contentEditor.getValue();
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/news/add',
                    {
                        is_used: this.news.isUsed,
                        title: this.news.title,
                        content: this.news.content,
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.$message({
                            message: '操作成功',
                            type: 'success'
                        });
                        var remember_me = res.body.OJ_USER_INFO.saved;
                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];

                        if (remember_me) {
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                        }

                        this.$router.push({
                            path: "/news/news/list",
                        })

                    } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                        this.$message.error('操作失败，您未登录');
                        window.location.href = "/admin/login.html";
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_NEWS) {
                        this.$message.error('无效的操作');
                    }
                });
            }
        },
        mounted() {
            this.contentEditor = new Vditor('vditor', {
                height: 500,
                cache: {
                    enable: false,
                },
                toolbarConfig: {
                    pin: true
                },
                tab: "\t",
                preview: {
                    math: {
                        inlineDigit: true
                    },
                    markdown: {
                        mark: true
                    },
                    hljs: {
                        style: "abap",
                        lineNumber: true
                    }
                },
                counter: {
                    enable: true,
                    type: "text"
                }
            })
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/news/home/add',
                {}, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    this.$message.error('操作失败，您未登录');
                    window.location.href = "/admin/login.html";
                }
            });
        }
    }
</script>

