                         text-color="#fff"
                         active-text-color="#ffd04b">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>公告</span>
                        </template>
                        <el-menu-item-group>
                            <router-link to="/news/news/list">
                                <el-menu-item index="1-1" v-bind:actives="$route.name.indexOf('news_') == 0">公告
                                </el-menu-item>
                            </router-link>
                            <el-menu-item index="1-2">轮播图</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="2">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>题目</span>
                        </template>
                        <el-menu-item-group>
                            <router-link to="/problem/problem/list">
                                <el-menu-item index="2-1" v-bind:actives="$route.name.indexOf('problem_') == 0">题目
                                </el-menu-item>
                            </router-link>
                            <el-menu-item index="2-2">分类 / 来源</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-menu-item index="3" disabled>
                        <i class="el-icon-document"></i>
                        <span slot="title">导航三</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-setting"></i>
                        <span slot="title">导航四</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
            <el-col style="padding-left: 240px" :span="24">
                <el-menu class="el-menu-demo" mode="horizontal" background-color="#545c64" text-color="#fff"
                         active-text-color="#ffd04b"
                         style="position: fixed;top: 0px;width: 100%;z-index: 1999">
                    <template v-if="$route.name.indexOf('news_') == 0">
                        <el-menu-item index="1" v-bind:actives="$route.name=='news_list'"
                                      @click="$router.push({path: '/news/news/list'})">公告列表
                        </el-menu-item>
                        <el-menu-item index="2" v-bind:actives="$route.name=='news_add'"
                                      @click="$router.push({path: '/news/news/add'})">添加公告
                        </el-menu-item>
                        <el-menu-item index="3" v-bind:actives="$route.name=='news_modify'"
                                      v-bind:disabled="$route.name!='news_modify'">修改公告
                        </el-menu-item>
                    </template>
                    <template v-if="$route.name.indexOf('problem_') == 0">
                        <el-menu-item index="1" v-bind:actives="$route.name=='problem_list'"
                                      @click="$router.push({path: '/problem/problem/list'})">题目列表
                        </el-menu-item>
                        <el-menu-item index="2"
                                      v-bind:actives="$route.name=='problem_add_content' || $route.name=='problem_add_option'"
                                      @click="$router.push({path: '/problem/problem/add_content'})">
                            添加题目
                        </el-menu-item>
                        <el-menu-item index="3"
                                      v-bind:actives="$route.name=='problem_modify_content' || $route.name=='problem_modify_option' || $route.name=='problem_modify_testcase'"
                                      v-bind:disabled="$route.name!='problem_modify_content' && $route.name!='problem_modify_option' && $route.name!='problem_modify_testcase'">
                            修改题目
                        </el-menu-item>
                    </template>
                </el-menu>
                <el-main>
                    <router-view></router-view>
