</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    /* eslint no-undef: "off"*/
    /* eslint no-unused-vars: "off"*/
    /* eslint vue/no-unused-components: "off"*/
    /* eslint vue/no-parsing-error: "off"*/
    /* eslint vue/no-duplicate-attributes: "off"*/
    import '@/assets/js/include.js'
    import fun from '@/assets/js/OJFunction'

    export default {
        name: 'App',
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    login: false
                },
                now: 1
            }
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            }
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");

            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/home/index',
                {
                    language: this.page_setting.language,
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;
                // console.log(error_type == this.GLOBAL.OJ_ERROR_NO_ERROR);
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.page_setting.login = true
                    // alert(this.page_setting.login)
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    window.location.href = "/admin/login.html";
                    // fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                }

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;
            });


        },
    }


</script>

<style lang="scss">
    body {
        margin: 0px;
