<template>
    <div id="problem-modify">
        <loadings></loadings>
        <el-card class="box-card" style="margin-top: 60px;">
            <el-steps :active="0" align-center>
                <el-step title="步骤1" description="修改题目内容"></el-step>
                <el-step title="步骤2" description="修改题目参数"></el-step>
            </el-steps>
            <div>
                <el-button @click="next_step" type="text" style="float: right">下一步 >></el-button>
            </div>
            <div style="clear: both"></div>
            <el-form ref="form" :model="problem" label-width="80px" style="margin-top: 15px;">
                <el-timeline>
                    <el-timeline-item placement="top" color="red">
                        <el-form-item label="标题">
                            <el-input @change="modify = true" v-model="problem.title" style="width: 50%">
                                <template slot="prepend">{{"ID "+ problem.problemId}}</template>
                            </el-input>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="orange">
                        <el-form-item label="创建时间">
                            <el-date-picker v-model="problem.createTime" type="datetime" placeholder="选择日期时间"
                                            :disabled="true">
                            </el-date-picker>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="yellow">
                        <el-form-item label="创建用户">
                            <el-input v-model="problem.creatorNick" :disabled="true" style="width: 30%"></el-input>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="#5FBD12">
                        <el-form-item label="题目描述">
                            <div id="problem-description"></div>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="blue">
                        <el-form-item label="输入描述">
                            <div id="input-description"></div>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="purple">
                        <el-form-item label="输出描述">
                            <div id="output-description"></div>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item v-for="(data, i) in problem.sampleIO" v-bind:key="key" placement="top"
                                      icon="el-icon-more" type="primary">
                        <el-card shadow="never">
                            <div slot="header" class="clearfix">
                                <span>样例{{" "+(i+1)}}</span>
                                <el-button @click="delSample(i)" v-bind:disabled="problem.sampleNum == 1"
                                           style="float: right;padding: 5px;"
                                           type="danger" icon="el-icon-delete" circle></el-button>
                                <div style="clear: both"></div>
                            </div>
                            <el-row>
                                <el-col :span="12" style="padding-right: 10px">
                                    <div class="grid-content bg-purple-dark">
                                        <el-form-item label="样例输入">
                                            <el-input type="textarea" :autosize="{ minRows: 8, maxRows: 16}"
                                                      v-model="data.sample_I" @change="modify = true"></el-input>
                                        </el-form-item>
                                    </div>
                                </el-col>
                                <el-col :span="12" style="padding-left: 10px">
                                    <div class="grid-content bg-purple-dark">
                                        <el-form-item label="样例输出">
                                            <el-input type="textarea" :autosize="{ minRows: 8, maxRows: 16}"
                                                      v-model="data.sample_O" @change="modify = true"></el-input>
                                        </el-form-item>
                                    </div>
                                </el-col>
                            </el-row>
                        </el-card>
                    </el-timeline-item>

                    <el-timeline-item placement="top" icon="el-icon-more" type="primary">
                        <el-button @click="addSample" type="primary" size="mini">添加更多样例...</el-button>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="purple">
                        <el-form-item label="提示">
                            <div id="hint"></div>
                        </el-form-item>
                    </el-timeline-item>
                    <el-timeline-item placement="top" color="purple">
                        <div style="text-align: center;margin-top: 30px;margin-bottom: 20px">
                            <span style="white-space: pre">您可以选择{{" "}}</span>
                            <el-button @click="edit(1, 1)" type="primary" size="small">保存修改</el-button>
                            <span style="white-space: pre">{{" "}}或保存修改并继续{{" "}}</span>
                            <el-button @click="edit(1, 2)" type="primary" size="small">修改题目参数</el-button>
                        </div>
                    </el-timeline-item>
                </el-timeline>
            </el-form>
        </el-card>

        <el-dialog title="你确定要删除吗？" :visible.sync="dialogVisible" width="30%">
            <span>需要保存当前的修改吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="edit(1, 2)">保存</el-button>
                <el-button type="danger" @click="edit(0, 2)">不保存</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    import '@/assets/js/include.js'
    import Loadings from '@/components/loading/loading'
    import fun from '@/assets/js/OJFunction'
    import Vditor from "vditor"

    export default {
        name: "modify",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: ""
                },
                problem: {
                    problemId: "",
                    title: "",
                    problemDescribe: "",
                    inputDescribe: "",
                    outputDescribe: "",
                    createTime: 0,
                    hint: "",
                    creatorNick: "",
                    sampleNum: 1,
                    sampleIO: [{
                        sample_I: "",
                        sample_O: ""
                    }]
                },
                vditor: {
                    problemDesciption: '',
                    inputDescription: '',
                    outputDescription: '',
                    hint: ''
                },
                dialogVisible: false,
                modify: false
            }
        },
        methods: {
            next_step() {
                if (this.modify == true ||
                    this.problem.problemDescribe != this.vditor.problemDesciption.getValue() ||
                    this.problem.inputDescribe != this.vditor.inputDescription.getValue() ||
                    this.problem.outputDescribe != this.vditor.outputDescription.getValue() ||
                    this.problem.hint != this.vditor.hint.getValue()) {
                    this.dialogVisible = true;
                } else {
                    this.edit(0, 2)
                }
            },
            edit(i, j) {//i代表是否保存，j代表传送到哪里
                if (i == 0) {
                    this.$router.push({
                        path: "/problem/problem/modify_option",
                        query: {
                            pid: this.$route.query.pid
                        }
                    })
                } else {
                    this.problem.problemDescribe = this.vditor.problemDesciption.getValue();
                    this.problem.inputDescribe = this.vditor.inputDescription.getValue();
                    this.problem.outputDescribe = this.vditor.outputDescription.getValue();
                    this.problem.hint = this.vditor.hint.getValue();
                    this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/modify_content',
                        {
                            problem_id: this.problem.problemId,
                            title: this.problem.title,
                            problem_describe: this.problem.problemDescribe,
                            input_describe: this.problem.inputDescribe,
                            output_describe: this.problem.outputDescribe,
                            hint: this.problem.hint,
                            sampleIO: JSON.stringify(this.problem.sampleIO),
                        }, {
                            emulateJSON: true,
                            headers: {
                                OJ_ACCESS_TOKEN: this.page_setting.access_token,
                                OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                            }
                        }).then(function (res) {
                        var error_type = res.body.OJ_ERROR_TYPE;
                        if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                            this.$message({
                                message: '操作成功',
                                type: 'success'
                            });
                            var remember_me = res.body.OJ_USER_INFO.saved;
                            var access_token = res.headers.map['oj_access_token'][0];
                            var refresh_token = res.headers.map['oj_refresh_token'][0];

                            if (remember_me) {
                                fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                                fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                            } else {
                                fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                                fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                            }

                            if (j == 1) this.$router.push({
                                path: "/problem/problem/list",
                            })
                            else if (j == 2) this.$router.push({
                                path: "/problem/problem/modify_option",
                                query: {
                                    pid: this.$route.query.pid
                                }
                            })

                        } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                            this.$message.error('操作失败，您未登录');
                            window.location.href = "/admin/login.html";
                        } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_NEWS) {
                            this.$message.error('无效的操作');
                        }
                    });
                }

            },
            addSample() {
                this.problem.sampleIO.push({
                    sample_I: "",
                    sample_O: ""
                })
                this.problem.sampleNum = this.problem.sampleIO.length
            },
            delSample(i) {
                this.problem.sampleIO.splice(i, 1);
                this.problem.sampleNum = this.problem.sampleIO.length
            }
        },
        mounted() {
            this.vditor.problemDesciption = new Vditor('problem-description', {
                height: 400,
                cache: {
                    enable: false,
                },
                toolbarConfig: {
                    pin: true
                },
                tab: "\t",
                preview: {
                    math: {
                        inlineDigit: true
                    },
                    markdown: {
                        mark: true
                    },
                    hljs: {
                        style: "abap",
                        lineNumber: true
                    }
                },
                counter: {
                    enable: true,
                    type: "text"
                }
            })
            this.vditor.inputDescription = new Vditor('input-description', {
                height: 400,
                cache: {
                    enable: false,
                },
                toolbarConfig: {
                    pin: true
                },
                tab: "\t",
                preview: {
                    math: {
                        inlineDigit: true
                    },
                    markdown: {
                        mark: true
                    },
                    hljs: {
                        style: "abap",
                        lineNumber: true
                    }
                },
                counter: {
                    enable: true,
                    type: "text"
                }
            })

            this.vditor.outputDescription = new Vditor('output-description', {
                height: 400,
                cache: {
                    enable: false,
                },
                toolbarConfig: {
                    pin: true
                },
                tab: "\t",
                preview: {
                    math: {
                        inlineDigit: true
                    },
                    markdown: {
                        mark: true
                    },
                    hljs: {
                        style: "abap",
                        lineNumber: true
                    }
                },
                counter: {
                    enable: true,
                    type: "text"
                }
            })
            this.vditor.hint = new Vditor('hint', {
                height: 400,
                cache: {
                    enable: false,
                },
                toolbarConfig: {
                    pin: true
                },
                tab: "\t",
                preview: {
                    math: {
                        inlineDigit: true
                    },
                    markdown: {
                        mark: true
                    },
                    hljs: {
                        style: "abap",
                        lineNumber: true
                    }
                },
                counter: {
                    enable: true,
                    type: "text"
                }
            })
        },
        created() {
            if (this.$route.query.pid == null || this.$route.query.pid == "") {
                this.$router.push({
                    path: "/problem/problem/list",
                })
            }
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");

            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/problem/home/modify_content',
                {
                    problem_id: this.$route.query.pid
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                console.log(res.body);
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.problem = res.body.OJ_PROBLEM
                    this.problem.sampleNum = this.problem.sampleIO.length;
                    this.vditor.problemDesciption.setValue(this.problem.problemDescribe);
                    this.vditor.inputDescription.setValue(this.problem.inputDescribe);
                    this.vditor.outputDescription.setValue(this.problem.outputDescribe);
                    this.vditor.hint.setValue(this.problem.hint);
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    window.location.href = "/admin/login.html";
                }
                $("html,body").animate({scrollTop: "0px"}, 0);
            }, function () {
                $("html,body").animate({scrollTop: "0px"}, 0);
            });
        }
    }
</script>

<style scoped>

</style>