<template>
    <div>
        <div class="layui-card">
            <div class="layui-card-header">公告列表</div>
            <div class="layui-card-body">
                <!--<button type="button" class="layui-btn layui-btn-primary" @click="haha">原始按钮</button>-->
                <table class="layui-hide" id="demo" lay-filter="demo"></table>
            </div>
        </div>
    </div>
</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    /* eslint no-undef: "off"*/
    /* eslint no-unused-vars: "off"*/
    import '@/assets/js/include.js'
    export default {
        name: "index",
        data(){
            return{
                news: null
            }
        },
        methods: {
            updata:function () {
                // this.test = [{
                //     "id": "10001"
                //     ,"username": "杜甫"
                //     ,"email": "xianxin@layui.com"
                //     ,"sex": "男"
                //     ,"city": "浙江杭州"
                //     ,"sign": "人生恰似一场修行"
                //     ,"experience": "116"
                //     ,"ip": "192.168.0.8"
                //     ,"logins": "108"
                //     ,"joinTime": "2016-10-14"
                // }];
                var _this = this
                layui.use('table', function(){
                    var table = layui.table;

                    //展示已知数据
                    table.render({
                        elem: '#demo'
                        ,defaultToolbar: ['filter', 'exports', 'print', { //自定义头部工具栏右侧图标。如无需自定义，去除该参数即可
                            title: '提示'
                            ,layEvent: 'LAYTABLE_TIPS'
                            ,icon: 'layui-icon-tips'
                        }]
                        ,cols: [[ //标题栏
                            {type:'checkbox',fixed:"left"}
                            ,{field: 'id', title: 'ID', width: 80, sort: true,fixed: 'left', unresize: true}
                            ,{field: 'title', title: '标题', width: 120}
                            ,{field: 'content', title: '内容', minWidth: 150}
                            ,{field: 'date', title: '创建时间', minWidth: 160}
                            ,{field: 'used', title: '是否发布', width: 80}
                            ,{field: 'user', title: '创建用户', width: 100}
                            ,{fixed: 'right', title:'操作', toolbar: '#barDemo', width:150}
                        ]]
                        ,data: _this.test
                        //,skin: 'line' //表格风格
                        ,even: true
                        ,page: true //是否显示分页
                        //,limits: [5, 7, 10]
                        //,limit: 5 //每页默认显示的数量
                    });
                    table.on('toolbar(demo)', function(obj){
                        var checkStatus = table.checkStatus(obj.config.id);
                        var data;
                        switch(obj.event){

                            case 'getCheckData':
                                data = checkStatus.data;
                                layer.alert(JSON.stringify(data));
                                break;
                            case 'getCheckLength':
                                data = checkStatus.data;
                                layer.msg('选中了：'+ data.length + ' 个');
                                break;
                            case 'isAll':
                                layer.msg(checkStatus.isAll ? '全选': '未全选');
                                break;

                            //自定义头工具栏右侧图标 - 提示
                            case 'LAYTABLE_TIPS':
                                layer.alert('这是工具栏右侧自定义的一个图标按钮');
                                break;
                        }
                    });

                    //监听行工具事件
                    table.on('tool(demo)', function(obj){
                        var data = obj.data;
                        console.log(obj)
                        if(obj.event === 'del'){
                            layer.confirm('真的删除行么', function(index){
                                obj.del();
                                layer.close(index);
                            });
                        } else if(obj.event === 'edit'){
                            layer.prompt({
                                formType: 2
                                ,value: data.email
                            }, function(value, index){
                                obj.update({
                                    email: value
                                });
                                layer.close(index);
                            });
                        }
                    });
                });
            }
        },
        created(){
            this.haha();
        }
    }
</script>

